.container{
    width: 607px;
    height: 804px;
    margin: auto;
    margin-top: 75px;
    border-radius: 12px;
    background-image:url(../Assets/pexels-greg-2418664.jpg);
}

.top-bar{
    display: flex;
    justify-content: center;
    gap:14px;
    padding-top:60px ;
}
.top-bar input{
    display: flex;
    width: 300px;
    height: 72px;
    background: rgb(203, 194, 194);
    border-radius: 40px;
    border: none;
    outline: none;
    font-size: 20px;
    font-weight: 400;
    padding-left: 40px;
    color: #626262;
}
.search-icon{
    width: 78px;
    height: 78px;
    border-radius: 50%;
    background: rgb(203, 194, 194);
    display: flex;
    align-items: center;
    justify-content: center;
}
.weather-image{
    margin-top: 29px;
    display: flex;
    justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 100px;
    font-weight: 400;
}
.location{
    display: flex;
    justify-content: center;
    font-size: 50px;
    font-weight: 400;
    color: white;
}
.data-container{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    color: white;
}
.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
}
.data{
    font-size: 20px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
}
.btn{
    margin-top:50px ;
    margin-left: 10px;
    border-radius: 10px;
    width: 100px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    cursor: pointer;
    background: rgb(6, 161, 68);
    color: white;
}
.recent-cities{
    /* margin: auto; */
    margin-left: 110px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width: 300px;
    max-height: 150px;
    background: rgb(198, 190, 190);
    outline: none;
    border-radius: 20px;
}

.recent-cities ul{
    display: flex;
    flex-direction: column;
    margin-left: 0%;
    padding-left: 0%;

}
.recent-cities ul li{
    list-style: none;
    font-size: 20px;
    font-weight: 400;
    color:rgb(122, 120, 120);
    border-bottom: 1px solid rgb(0, 0, 0);
    border-top: .5px solid rgb(0, 0, 0);
}